}

body {
  font-family: Goblin One;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: #9a9a9a; }

#root {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; }

.inner-container {
  outline: 5px solid greenyellow;
  position: relative;
  width: 660px;
  text-align: center;
  background-color: #b3b3b3; }
  .inner-container .heading {
    margin-top: 20px; }
  .inner-container .pad-bank {
    width: 332px;
    height: 272px;
    display: inline-block;
    margin: 20px; }
    .inner-container .pad-bank .drum-pad {
      position: relative;
      float: left;
      width: 100px;
      height: 80px;
      margin-right: 10px;
      border-radius: 10px;
      padding-top: 35px;
      box-sizing: border-box;
      cursor: pointer; }
  .inner-container .controls-container {
    width: 240px;
    height: 112px;
    display: inline-block;
    margin: 40px 20px 0 10px;
    vertical-align: top; }
    .inner-container .controls-container #display {
      width: 220px;
      background-color: gray;
      margin: 15px auto;
      padding: 15px;
      box-sizing: border-box;
      align-items: stretch; }

@media screen and (max-width: 650px) {
  .inner-container {
    -webkit-transform: scale(0.7);
            transform: scale(0.7); } }

